.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  @media screen and (min-width: 480px) {
    width: 480px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    margin: 0 auto;
  }
  @media screen and (min-width: 1200px) {
    width: 1200px;
  }
}
